.header {
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 50;
  transition:
    position 0.3s ease-in-out,
    box-shadow 0.3s ease-in-out;
  background-color: transparent;
  box-shadow: 0;
  &.scrolled {
    position: fixed;
    background-color: $color-white;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.1);
    .header-top__container {
      padding-top: 20px;
    }
    .nav {
      @include breakpoint(tablet) {
        padding-top: 34px;
      }
    }

    a.contact-link.btn-contact-link {
      background-color: $color-ultra-light;
    }
  }
  &.header-open {
    background-color: $color-white;
  }

  &-top {
    &__container {
      padding: 40px 0 20px;
      column-gap: 24px;
      transition: padding-top 0.3s ease-in-out;
      @include breakpoint(tablet) {
        padding-top: 60px;
        column-gap: 0;
      }
    }
    &__nav {
      margin: 0 auto;
      .nav__link {
        color: $color-light-gray;
        white-space: nowrap;
        &:hover,
        &:active,
        &:focus {
          color: $color-dark;
        }
      }
    }
  }

  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include breakpoint(tablet) {
      justify-content: flex-start;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: min-content;
    padding: 14px 24px;
    border-radius: $radius-16;
    background-color: $accent;
    .icon,
    svg {
      display: block;
      width: 100px;
      height: 24px;
    }

    &-group {
      display: flex;
      align-items: center;
      border-radius: $radius-16;
      background-color: $accent;
      .header__logo {
        position: relative;

        @include breakpoint(tablet) {
          padding: 14px 12px;
        }
        &:before {
          position: absolute;
          content: "";
          width: 1px;
          height: 30px;
          border-radius: 4px;
          top: 11px;
          left: 0;
          background: $color-white;
          opacity: 0.5;
        }
      }
      & + .contact-link {
        margin-left: auto;
      }
    }
  }

  &__menu-group {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 0;
    background: transparent;
    .header__logo {
      padding: 0;
      background: transparent;
    }
  }

  &__buttons {
    display: flex;
    column-gap: 20px;

    @include breakpoint(tablet) {
      width: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 16px;
      row-gap: 16px;
    }
  }

  a.contact-link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    border-radius: $radius-12;
    
    &.btn-contact-link {
      background-color: $color-white;
      column-gap: 10px;
      @include breakpoint(tablet) {
        border-radius: $radius-16;
        background: $color-ultra-light;
      }

      span {
        font-family: $TTNorms-Bold;
        font-size: 16px;
      }
    }
    .icon {
      width: 20px;
      height: 20px;

      &.contact-link-telegram {
        fill: $color-dark;
      }

      &.contact-link-mail {
        path {
          fill: $color-white;
        }
      }
    }

    &:hover {
      .icon {
        &.contact-link-telegram {
          fill: $color-dark-gray;
        }
      }
    }
  }

  .btn-accent {
    @include breakpoint(desktop) {
      padding: 15px 24px;
    }
    @include breakpoint(tablet) {
      margin: 0 auto;
    }
  }

  .btn-accent,
  .btn-contact-link {
    @include breakpoint(tablet) {
      width: 100%;
      max-width: 400px;
    }
  }
}

.burger {
  position: relative;
  width: 48px;
  height: 51px;
  @include none;
  border: none;
  padding: 14px;
  border-radius: $radius-16;
  background-color: $accent;
  transition:
    transform 0.45s ease,
    position 0.35s ease;
  cursor: pointer;

  &__line {
    position: absolute;
    left: 14px;
    top: 50%;
    width: 20px;
    height: 3px;
    border-radius: 3px;
    background-color: $color-white;
    transform: translateY(-50%);
    transition: opacity 0.25s ease-in-out;
  }
  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 14px;
    width: 20px;
    height: 3px;
    border-radius: 3px;
    background-color: $color-white;
    transition:
      transform 0.25s ease-in-out,
      top 0.25s ease-in-out,
      bottom 0.25s ease-in-out;
  }
  &::before {
    top: 18px;
  }
  &::after {
    bottom: 18px;
  }
